.p-home {
	width: 100%;
	height: 100%;
	overflow: hidden;
	.footer-top {
		@include max-height(820px) {
			display: none; } } }

// disable section-scroll class
.disable-auto-scroll {
	overflow: visible!important;
	body {
		overflow: visible!important; }
	.wrapper {
		height: auto!important;
		overflow: visible!important; }
	.p-home {
		position: relative;
		height: auto;
		overflow: hidden; }
	#section-wrap {
		touch-action: auto!important;
		overflow-y: visible!important;
		height: auto; }
	.section {
		overflow: hidden;
		position: static;
		transition: none;
		transform: translate(0, 0) !important;
		min-height: 400px;
		padding: 40px 0; }
	.to-top,
	.to-left,
	.to-right,
	.to-bottom {
		&:before, &:after {
			display: none!important; } }

	.section-hero {
		height: 100vh; }
	.parallax-block__img-w {
		img {
			transform: translate(0, 0)!important; } }
	.parallax-block__more {
		margin-top: 50px; } }
//--- end disable section-scroll class

.hero-title {
	position: relative;
	font-size: 60px;
	transform: translateZ(0px);
	z-index: 2;
	@include max-screen($notebook-width) {
		font-size: 46px; }
	@include max-screen($mid-tablet) {
		font-size: 40px; }
	@include max-screen($small-tablet) {
		font-size: 8vmin; }

	&__link {
		display: inline-block;
		position: relative;
		&.is-active {
			&:after {
				height: 5px; } }
		&:after {
			display: block;
			content: "";
			position: absolute;
			left: 0;
			bottom: 3px;
			width: 100%;
			height: 2px;
			background: $color-white;
			transition: height .3s; } } }

#section-wrap {
	position: relative;
	width: 100%;
	height: 100%;
	overflow: hidden; }

.section {
	@include bg-cover;
	//height: 100vh
	//min-height: 600px
	//min-height: 560px
	min-height: 500px;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	//padding: 100px 0 40px
	padding: 120px 0 40px;

	display: flex;
	flex-direction: column;
	justify-content: center;
	overflow: hidden;
	&__title {
		font-size: 44px;
		text-align: center;
		margin-bottom: 75px;
		@include max-screen($big-tablet) {
			font-size: 40px; }
		@include max-screen($small-tablet) {
			font-size: 32px; }
		@include max-height(680px) {
			margin-bottom: 40px; } }

	//&[data-from="bottom"]
	//	transform: translateY(100%)
	//&[data-from="left"]
	//	transform: translateX(-100%)
	//	.parallax-block__img-w img
	//		//transform: translateX(20%)
	//&[data-from="right"]
	//	transform: translateX(100%)
	//&[data-from="top"]

	> .container {
		//flex: 1
		height: 100%;
		// test align below
		display: flex;
		flex-direction: column;
		justify-content: center; }

	.row {
		@include max-screen($notebook-width) {
			margin-left: -150px;
			margin-right: -150px; }
		@include max-screen($big-tablet) {
			margin-left: -75px;
			margin-right: -75px; }
		@include max-screen($common-tablet) {
			margin-left: -20px;
			margin-right: -20px; } }
	.container {
		@include max-screen($notebook-width) {
			padding-left: 150px;
			padding-right: 150px; }
		@include max-screen($big-tablet) {
			padding-left: 75px;
			padding-right: 75px; }
		@include max-screen($common-tablet) {
			padding-left: 20px;
			padding-right: 20px; } } }


.section-hero {
	background-image: url("../img/slide-hero.jpg");
	color: $color-white;
	z-index: 1;
	&:before {
		display: block;
		content: "";
		opacity: 0;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.4);
		transition: opacity .2s;
		z-index: 1; }
	&--overlay:before {
		opacity: 1; }
	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		img {
			@include object-fit(cover);
			width: 100%;
			height: 100%; } }
	.row {
		display: flex;
		align-items: center;
		height: 100%; } }

