.parallax-block {
	position: relative;
	height: 100%;
	@include max-screen(1500px) {
		padding-left: 80px; }
	@include max-screen($small-tablet) {
		padding-left: 20px; }
	&__img-w {
		//+make-col-offset(1)
		//margin-left: 90px
		height: 100%;
		overflow: hidden;
		img {
			@include object-fit(cover);
			width: 100%;
			height: 100%; } }
	&__text {
		position: absolute;
		top: 50%;
		//left: 0
		left: -90px;
		transform: translateY(-50%) translateZ(0px);
		z-index: 100;
		@include max-screen(1500px) {
			left: 0; } }
	&__title {
		position: relative;
		font-size: 60px;
		max-width: 640px;
		text-transform: uppercase;
		@include max-screen($big-tablet) {
			font-size: 48px; }
		@include max-screen($small-tablet) {
			font-size: 40px; }
		@include max-screen($smartphone-landscape) {
			font-size: 7vmin; }

		&.to-top {
			&:after {
				@include b;
				bottom: 100%;
				left: 0;
				width: 2px;
				height: 10000px;
				background: $color-black; } } }
	&__subtitle {
		max-width: 640px;
		margin-top: 70px;
		@include max-screen($smartphone-landscape) {
			margin-top: 40px;
			font-size: 5vmin;
			min-height: 90px; } } // test
	&__more {
		position: relative;
		display: inline-block;
		font-family: "geo-bold";
		text-transform: uppercase;
		margin-top: 100px;
		transition: transform .3s ease-in-out;
		&:hover, &:focus {
			transform: translateX(20px); }
		@include max-screen($smartphone-landscape) {
			margin-top: 60px; }
		&.to-left {
			&:before {
				@include b;
				top: calc(50% - 2px);
				right: calc(100% + 12px);
				width: 1000px;
				height: 2px;
				background: $color-black; } }
		&.to-right {
			&:before {
				@include b;
				top: calc(50% - 2px);
				left: calc(100% + 12px);
				width: 4000px;
				height: 2px;
				background: $color-black; } }
		&.to-bottom {
			&:after {
				transition: transform .3s ease-in-out;
				transform: translateX(0px); }
			&:hover, &:focus {
				&:after {
					transform: translateX(-20px); } }
			&:after {
				@include b;
				top: 100%;
				left: 0;
				width: 2px;
				height: 10000px;
				background: $color-black; } }
		&.to-top {
			&:after {
				@include b;
				bottom: 100%;
				left: 0;
				width: 2px;
				height: 10000px;
				background: $color-black; } } } }

