 }	//height: 140vh

.home-nav {
	position: fixed;
	top: 50%;
	transform: translateY(-50%);
	right: 10px;
	//right: 0
	z-index: 3;
	border-color: $color-white;
	@include max-screen($common-tablet) {
		display: none; }

	@include max-screen($small-tablet) {
		right: 0; }
	&__item {
		position: relative;
		width: 40px;
		height: 40px;
		box-sizing: content-box;
		cursor: pointer;
		border-color: inherit;
		@include max-screen($small-tablet) {
			width: 20px; }
		&.is-active {
			&:before {
				width: 8px;
				height: 8px; } }
		&:before {
			@include centre(both);
			display: inline-block;
			content: "";
			border-width: 2px;
			border-style: solid;
			border-color: inherit;
			width: 0;
			height: 0;
			transition: all .2s ease-in; } } }

.scroll-down {
	position: absolute;
	left: 50%;
	transform: translateX(-50%) translateZ(0px);
	bottom: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	z-index: 2;
	&__caption {
		text-align: center;
		font-family: "geo-bold";
		font-size: 12px;
		color: $color-white;
		text-transform: uppercase;
		margin-bottom: 15px; }
	&__line {
		margin: 0 auto;
		width: 2px;
		height: 90px;
		background: $color-white;
		@include max-screen($mid-tablet) {
			height: 30px; } } }


// hover right-bar
.hero-panel {
	opacity: 0;
	//position: absolute
	position: fixed;
	right: 0;
	top: 0;
	width: 420px;
	height: 100%;
	z-index: 10;
	background: $color-gallery;
	transform: translateX(100%);
	@include max-screen($common-tablet) {
		display: none; }
	&__inner {
		height: 100%; }
	&__list {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		li {
			position: relative;
			height: 25%;
			max-height: 270px;
			.project-card,
			.project-card__img-w {
				height: 100%;
				img {
					@include object-fit(cover);
					width: 420px;
					height: 100%; } } } } }

