.trans-slowerest {
	transition: all 1400ms ease-in-out;
	transition-timing-function: cubic-bezier(.16,.01,.77,1); }

.slick-effect {
	@extend .trans-slowerest;
	position: relative;
	height: 100%;
	overflow: hidden;
	z-index: 1;
	transform: translateX(140%);
	&__img {
		@extend .trans-slowerest;
		transform: translateX(-100%); } }

.slick-slide.slick-active .slick-effect,
.slick-slide.slick-active .slick-effect__img {
	transform: translateX(0%); }

.slick-slide.slick-out .slick-effect {
	transform: translateX(-40%)!important; }
.slick-slide.slick-out .slick-effect__img {
	transform: translateX(25%)!important; }

